"use client";

export default function TitleBar() {
  const api = typeof window !== "undefined" ? window.electron : undefined;

  return (
    <div className="titlebar-drag flex h-12 items-center justify-between border-b border-white/10 bg-white/[0.03] px-3">
      {/* Left: 앱 아이콘 영역(placeholder) */}
      <div className="flex items-center gap-2">
        <div className="h-6 w-6 rounded-md border border-white/10 bg-white/10" />
        <div className="h-2.5 w-28 rounded-full bg-white/10" />
      </div>

      {/* Right: window controls */}
      <div className="titlebar-nodrag flex items-center gap-2">
        <button
          onClick={() => api?.window.minimize()}
          className="h-8 w-10 rounded-md border border-white/10 bg-white/5 hover:bg-white/10"
          aria-label="Minimize"
        />
        <button
          onClick={() => api?.window.toggleMaximize()}
          className="h-8 w-10 rounded-md border border-white/10 bg-white/5 hover:bg-white/10"
          aria-label="Toggle maximize"
        />
        <button
          onClick={() => api?.window.close()}
          className="h-8 w-10 rounded-md border border-white/10 bg-white/5 hover:bg-white/10"
          aria-label="Close"
        />
      </div>
    </div>
  );
}
